#home-header {
  background-color: #e9203d;
  padding: 10px;
}
#home-header * {
  color: #fff;
  font-size: 16px;
  line-height: 1;
}
.home-header-left {
  width: 60px;
  text-align: left;
  margin: 5px 0;
}
.home-header-middle {
  width: auto;
  margin: 0 30px 0 60px;
}
.home-header-middle .search-container {
  background-color: #fff;
  border-radius: 15px;
  overflow: hidden;
  padding: 5px;
}
.home-header-middle .search-container i {
  color: #ccc !important;
}
.home-header-right {
  width: 30px;
  margin: 5px 0;
  text-align: right;
}



