.Tabwrap {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 44px;
  background: #FFF;
}
.TabItem {
  width: 33.333%;
  display: inline-block;
  text-align: center;
}
.TabItem i {
  display: block;
  font-size: 18px;
  color: #666;
}
.TabItem span {
  font-size: 12px;
  line-height: 22px;
  color: #666;
}



